.container {
  display: inline-block;
  position: relative;
  width: 200px;
  height: 200px;
  margin: auto;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-animation: run 8s;
          animation: run 8s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  z-index: 2; }

.container div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  line-height: 200px;
  text-align: center;
  opacity: 0.9; }

.container .f1 {
  background: red;
  -webkit-transform: translateZ(100px);
          transform: translateZ(100px); }

.container .f2 {
  background: green;
  -webkit-transform: translateZ(-100px) rotateY(180deg);
          transform: translateZ(-100px) rotateY(180deg); }

.container .f3 {
  background: yellow;
  -webkit-transform: translateX(-100px) rotateY(-90deg);
          transform: translateX(-100px) rotateY(-90deg); }

.container .f4 {
  background: purple;
  -webkit-transform: translateX(100px) rotateY(90deg);
          transform: translateX(100px) rotateY(90deg); }

.container .f5 {
  background: blue;
  -webkit-transform: translateY(100px) rotateX(-90deg);
          transform: translateY(100px) rotateX(-90deg); }

.container .f6 {
  background: grey;
  -webkit-transform: translateY(-100px) rotateX(90deg);
          transform: translateY(-100px) rotateX(90deg); }

@-webkit-keyframes run {
  0% {
    -webkit-transform: rotateX(0deg) rotateY(0deg);
            transform: rotateX(0deg) rotateY(0deg); }
  14.3% {
    -webkit-transform: rotateX(45deg) rotateY(45deg);
            transform: rotateX(45deg) rotateY(45deg); }
  28.6% {
    -webkit-transform: rotateX(90deg) rotateY(90deg);
            transform: rotateX(90deg) rotateY(90deg); }
  42.9% {
    -webkit-transform: rotateX(180deg) rotateY(180deg);
            transform: rotateX(180deg) rotateY(180deg); }
  57.1% {
    -webkit-transform: rotateX(225deg) rotateY(225deg);
            transform: rotateX(225deg) rotateY(225deg); }
  71.4% {
    -webkit-transform: rotateX(270deg) rotateY(270deg);
            transform: rotateX(270deg) rotateY(270deg); }
  85.7% {
    -webkit-transform: rotateX(315deg) rotateY(315deg);
            transform: rotateX(315deg) rotateY(315deg); }
  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg);
            transform: rotateX(360deg) rotateY(360deg); } }

@keyframes run {
  0% {
    -webkit-transform: rotateX(0deg) rotateY(0deg);
            transform: rotateX(0deg) rotateY(0deg); }
  14.3% {
    -webkit-transform: rotateX(45deg) rotateY(45deg);
            transform: rotateX(45deg) rotateY(45deg); }
  28.6% {
    -webkit-transform: rotateX(90deg) rotateY(90deg);
            transform: rotateX(90deg) rotateY(90deg); }
  42.9% {
    -webkit-transform: rotateX(180deg) rotateY(180deg);
            transform: rotateX(180deg) rotateY(180deg); }
  57.1% {
    -webkit-transform: rotateX(225deg) rotateY(225deg);
            transform: rotateX(225deg) rotateY(225deg); }
  71.4% {
    -webkit-transform: rotateX(270deg) rotateY(270deg);
            transform: rotateX(270deg) rotateY(270deg); }
  85.7% {
    -webkit-transform: rotateX(315deg) rotateY(315deg);
            transform: rotateX(315deg) rotateY(315deg); }
  100% {
    -webkit-transform: rotateX(360deg) rotateY(360deg);
            transform: rotateX(360deg) rotateY(360deg); } }
